<template>
  <div class="hello">
    <form @submit.prevent="addTask(task)">
      <el-input type="text" placeholder="Veuillez entrer una tâche à effectuer" v-model="task" name="task"></el-input>
    </form>
    <ul>
      <transition-group name="list" enter-active-class="animated bounceInLeft" leave-active-class="animated fadeOut">
        <li v-for="(data, idx) in tasks" :key="idx">
          <i class="el-icon-check"></i>
          {{data.task}}
          <i class="el-icon-delete right" @click="removeTask(data.id)"></i>
          <p>Crée par <span class="creator">Megapunkdev</span>.</p>
        </li>
      </transition-group>
    </ul>
  </div>
</template>

<script>

import { db } from '../main'

export default {
  name: 'Todo',
  data () {
    return {
      task: '',
      tasks: [
      ]
    }
  },
  firestore () {
    return {
      tasks: db.collection('tasks').orderBy('createdAt')
    }
  },
  methods: {
    addTask (task) {
      const createdAt = new Date()
      db.collection('tasks').add({ task, createdAt })
      this.task = ''
    },
    removeTask (id) {
      db.collection('tasks').doc(id).delete()
    }
  }
}
</script>

